import React, { Component } from 'react';

class ArtistProfile extends Component {
    constructor(props) {
        super(props);
        this.state = {}

    }


    render() {
        let { artistProfile, artPieces } = this.props.artistObj;
        let ftArtPiece = Object.keys(artPieces)[0];
        let imgArr = artPieces[ftArtPiece]['imgUrls'];
        let imgKey = Object.keys(imgArr)[0];
        let imgUrl = imgArr[imgKey];

        return (
            <div className="artistBlock">
                <div className="text-center col-sm-3">
                    <img src={imgUrl} className="featuredImg" alt=""/>
                </div>
                <div className="rightCol col-sm-9 text-center">
                    <p className="publicName">{artistProfile.publicName}</p>
                    <p className="fname">Name: {artistProfile.fname}</p>
                    <p className="ig">IG: <a href={'https://instagram.com/' + artistProfile.igUsername} target="_blank" rel="noopener noreferrer">{artistProfile.igUsername}</a></p>
                </div>
            </div>
        )
    }
}


export default ArtistProfile;


