import { GET_FEATURED_PIECES, GET_ARTISTS, SUBSCRIBE_USER } from '../constants';
import axios from 'axios';

export const getFeaturedPieces = () => {
    const action = {
        type: GET_FEATURED_PIECES,
        pieces: 'Yay'
    }
    return action;
}

export const subscribeEmail = (userEmail) => {
    return (dispatch) => {
        return axios.get('https://us-central1-art-auction-2ef27.cloudfunctions.net/newEmailSub?email=' + userEmail).then((response) => {
            console.log(response.status);
            dispatch({
                type: SUBSCRIBE_USER,
                response: response.data
            })
        })
    }
}

export const getArtists = () => {

    return (dispatch) => {
        return axios.get('https://us-central1-art-auction-2ef27.cloudfunctions.net/getAllArtists').then((response) => {
            dispatch(allArtists(response.data));
        })
    }

}

export function allArtists(artists) {
    return {
        type: GET_ARTISTS,
        artists
    }
}


